 <script>
export default {
  name: "HelloWorld",
  data() {
    return {
      text: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin fermentum, ligula ac accumsan lobortis, nulla ante pharetra magna, sed sagittis dui metus sit amet lorem. ",
      callbackText: "",
      customStyle: {
        flashMessageStyle: {
          background: "linear-gradient(#e66465, #9198e5)",
        },
        iconStyle: {
          backgroundImage: `url(data:image/svg+xml;base64,)`,
        },
      },

      countries: [],
      selected: "",
      loading: false,
      search: {
        citizen: "",
        from: "",
        to: "",
      },
      feedback: {
        countryid: "",
        username: "",
        email: "",
        isnotify: true,
      },

      successFeedback: false,

      visasNonEntry: {},
      visas: {},
      covidInfo: "",
    };
  },
  mounted() {
    console.log(process.env.VUE_APP_API_URL);

    fetch(process.env.VUE_APP_API_URL + "Countries")
      .then((res) => res.json())
      .then((data) => {
        this.countries = data;
        console.info(data);
      })
      .catch((err) => console.log(err.message));
  },
  methods: {
    search1() {
      fetch(
        process.env.VUE_APP_API_URL +
          "Visas/GetNonEntry?DestinationId=" +
          220 +
          "&PassportId=" +
          this.search.citizen
      )
        .then((res) => res.json())
        .then((data) => {
          this.visasNonEntry = data;
          console.info(data);
        })
        .catch((err) => {
          console.warn(err.message);
          this.visasNonEntry = null;
        });

      fetch(process.env.VUE_APP_API_URL + "Visas")
        .then((res) => res.json())
        .then((data) => {
          this.visas = data;
          console.info(data);
        })
        .catch((err) => {
          console.warn(err.message);
          this.visas = [];
        });

      fetch(process.env.VUE_APP_API_URL + "Countries/GetCovidInfo?countryId=" + 220)
        .then(res => res.text())
        .then(text => this.covidInfo = text )
        .catch((err) => {
          console.warn(err.message);
        });

      //      console.info("Visas", this.visas)
      //console.info("Visas Non", this.visasNonEntry)
    },

    async SendFeedback() {
      try {
        const request = new Request(
          process.env.VUE_APP_API_URL + "Countries/Feedback",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(this.feedback),
          }
        );

        const res = await fetch(request);

        if (res.status == 200 || res.status == 201) {
          this.successFeedback = true;
        }
      } catch (error) {
        console.warn(error);
      }
    },
  },
};
</script>


